import React from 'react'
import { useLocation } from 'react-router-dom'
import { Row, Col, Container, Button } from 'react-bootstrap';
import { styled } from 'styled-components';
import { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHeart } from '@fortawesome/free-regular-svg-icons';
import { faCartArrowDown } from '@fortawesome/free-solid-svg-icons';
import { useRef } from 'react';

import { useDispatch, useSelector } from 'react-redux';
import { addItem } from '../data/store';

/* my */
import './detail.scss'


const ButtonQt = styled.button`
  width: 30px;
  height: 30px;
  box-sizing: border-box;
  border: 1px solid #aaa;
`



export default function Detail() {
  const location = useLocation();
  const item = location.state.product

    
    /*  /  */
    const [quant, setQuant] = useState(1)
    const [itemprice, setPrice] = useState(item.salePrice)
    const priceChange = function(v){
      if(v<1){
        alert(' 1   .')
        return
      }
      setQuant(v)
      setPrice(v*item.salePrice)
    }

    /* redux */
    const state = useSelector(state=>state)
    const dispatch = useDispatch()
    


    
    //li_scroll
    const itemDetail = useRef()
    const purchaseInfo = useRef()
    const review = useRef()
    const qna = useRef()
    const relative = useRef()

    const scroll = (e)=>{
      e.current.scrollIntoView()
    }
    

  return (
    <>
    <Container className="detail">
        <Row className='info_top'>
          <Col xs={12} md={6} lg={5} className='left'>
            <img src={item.img}></img>
          </Col>        
          <Col xs={12} md={6} lg={5} className='right'>
            <h1 className='name'>{item.name}</h1>
            <div className='price'>
            <p className='origin_price'>{item.originPrice.toLocaleString()}</p>
            <p className='sale_price'>{item.salePrice.toLocaleString()}</p>
            </div>

            <div className='buy_action'>            
              <div className="quant">
                <p className='btn_title'> </p>
                <div className='btn_cover'>
                <ButtonQt type="button"
                  onClick={()=>{priceChange(quant-1)}}
                >-</ButtonQt>
                <input type="number" value={quant}
                  onChange={(e)=>{
                    priceChange(parseInt(e.target.value))
                  }}
                ></input>
                <ButtonQt type="button"
                  onClick={()=>{priceChange(quant+1)}}
                >+</ButtonQt></div>
              </div>{/* quant */}

                <div className='item_total'>
                    <p className='btn_title'>  </p>
                  <div>
                    <p className='total_price'>{itemprice.toLocaleString()}</p>
                  </div>
                </div>

                <div className='add_btn'>
                  <Button> </Button>
                  <Button><FontAwesomeIcon icon={faHeart} /></Button>
                  <Button 
                    onClick={()=>{dispatch(addItem({id: item.id, img: item.img, name: item.name, salePrice: item.salePrice, quant: quant}
                    ))}}>
                      <FontAwesomeIcon icon={faCartArrowDown}/>
                  </Button>
                </div>

            </div>{/* //buy_action */}
          </Col>
        </Row>

          <Row className='info_bottom'>
            <Col xl={12} className='item_detail' ref={itemDetail}>
                <ul className='info_bar'>
                  <li className='on' onClick={()=>{scroll(itemDetail)}}>  </li>
                  <li onClick={()=>{scroll(purchaseInfo)}}> </li>
                  <li onClick={()=>{scroll(review)}}> </li>
                  <li onClick={()=>{scroll(qna)}}> </li>
                  <li onClick={()=>{scroll(relative)}}> </li>
                </ul>
              <img src={process.env.PUBLIC_URL + '/images/detail/d1.jpg'}></img>
            </Col>

            <Col xl={12} className='purchase_info' ref={purchaseInfo}>
              <ul className="info_bar">
                <li onClick={()=>{scroll(itemDetail)}}>  </li>
                <li onClick={()=>{scroll(purchaseInfo)}} className='on'> </li>
                <li onClick={()=>{scroll(review)}}> </li>
                <li onClick={()=>{scroll(qna)}}> </li>
                <li onClick={()=>{scroll(relative)}}> </li>
              </ul>

              <ul>
                <h6 className='col_tit'> </h6>
                <li className='li_tit'>* </li>
                <li> : ,CJ (,)</li>
                <li> :    30,000   3,000 .</li>
                <li>       .</li>
                <li> 3        .</li>
                <li>,        .(    .)</li>
                <li>    2~3 (,  )   ,           .</li>
              </ul>

                <ul>
                  <li className='li_tit'>*  </li>
                  <li> (  ) 5,000 .</li>
                  <li> 2     .</li>
                  <li>/     .</li>
                  <li>    .</li>
              </ul>
            </Col>

            <Col xl={12} className='review' ref={review}>
              
            <ul className="info_bar">
                <li onClick={()=>{scroll(itemDetail)}}>  </li>
                <li onClick={()=>{scroll(purchaseInfo)}}> </li>
                <li onClick={()=>{scroll(review)}}  className='on'> </li>
                <li onClick={()=>{scroll(qna)}}> </li>
                <li onClick={()=>{scroll(relative)}}> </li>
              </ul>

              <h6 className='col_tit'> </h6>
            </Col>

            <Col xl={12} className='qna' ref={qna}>
              
            <ul className="info_bar">
                <li onClick={()=>{scroll(itemDetail)}}>  </li>
                <li onClick={()=>{scroll(purchaseInfo)}}> </li>
                <li onClick={()=>{scroll(review)}}> </li>
                <li onClick={()=>{scroll(qna)}}  className='on'> </li>
                <li onClick={()=>{scroll(relative)}}> </li>
              </ul>

              <h6 className='col_tit'> </h6>
            </Col>

            <Col xl={12} className='relative' ref={relative}>
              
            <ul className="info_bar">
                <li onClick={()=>{scroll(itemDetail)}}>  </li>
                <li onClick={()=>{scroll(purchaseInfo)}}> </li>
                <li onClick={()=>{scroll(review)}}> </li>
                <li onClick={()=>{scroll(qna)}}> </li>
                <li onClick={()=>{scroll(relative)}}  className='on'> </li>
              </ul>

              <h6 className='col_tit'> </h6>
                   ? 
               -2 -1 +1 +2   
            </Col>
          </Row>



      </Container>
    </>
  )
}

